.player_container {
  width: 50%;
  margin: 0 auto;
}

.start_form {
  position: absolute;
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
  z-index: 1;
  background-color: white;
  border: 3px solid;
  border-color: #432b68;
  padding: 4px;
  height: 60%;
}

.thanks_title {
  text-align: center;
  font-size: 72px;
}
.head_title {
  text-align: center;
  font-size: 42px;
}

button {
  margin: 3px;
}

.end_form {
  margin-top: 10px;
}



